.section {
  width: 100%;
  float: left;
  overflow: hidden;
}
/* 内容部分 */
.section-title {
  text-align: center;
  color: #FFF;
  text-transform: uppercase;
  font-weight: normal;
  padding: 30px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  width: 100%;
  clear: both;
}

.section--more {
  color: #FFF;
  background: #0E0F34;
  padding: 20px 0 40px 0;
}

.section-group {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 30px 0;
}

.section-group h3 {
  margin: 0 0 20px 0;
  text-transform: uppercase;
}

.section-group h4 {
  font-weight: normal;
  color: rgba(255, 255, 255, 0.6);
}

.section-group code {
  padding: 20px;
  font-size: 1.2em;
}

.section-group .btn {
  margin-right: 20px;
}

/* 文字 */
.code {
  width: 40%;
  clear: both;
  height: 200px;
  background: #FFF;
  border-radius: 2px;
  margin: 12vh 0;
  padding: 10px;
}

.code pre,
.code code {
  height: 100%;
  margin: 0;
  padding: 20px 0 0 20px;
}

.code--small code {
  padding-top: 75px;
}

.code--left {
  float: left;
}

.code--right {
  float: right;
}

.code--wider {
  width: 60%;
}

.code--wider code {
  padding-top: 60px;
}

