/* 主体 */
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif, Helvetica, Tahoma;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e4d7fd;
  min-height: 100vh;
  background-image: url(../img/bg.jpeg);
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
}

/* 主要容器 */
.container {
  width: 970px;
  margin: 0 auto;
  max-width: 90%;
  -webkit-transform: translateZ(1000px);
  transform: translateZ(1000px);
}

/* 盒子模型表现一致 */
*,
*:before,
*:after {
  box-sizing: border-box;
}

/* a标签 */
a {
  color: #FF4081;
  margin: 0;
  padding: 0;
  text-align: center;
  text-decoration: none;
}
/* p标签 */
p {
  margin: 0;
  padding: 0;
  text-align: center;
  text-decoration: none;
}
/* 文字code */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* 页面 */
#home,
#list,
#detail,
#content {
  padding-bottom: 40px;
  max-width: 1200px;
  overflow-x: hidden;
  margin: auto;
}

#mainbox {
  padding-bottom: 120px;
}


/* #newlist {
  max-width: 1200px;
