/* 页脚 */
#footers {
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: rgba(227, 164, 242, 0.8);
  line-height: 30px;
  font-size: 20px;
  width: 100%;
  display: block;
  text-align: center;
}

/* 页脚按钮 */
#footers .btn {
  width: 210px;
  display: inline-block;
  line-height: 40px;
  text-align: center;
  font-size: 12px;
  background-color: #2d1856;
  color: #fff;
  padding: 0 10px;
  border-radius: 20px;
  margin: 20px 40px;
  border: 1px solid #333;
  cursor: pointer;
}

/* 轮播图片 */
.slick-slider img {
  display: block;
  width: 100%;
}

/* .adsbygoogle {
  display: none;
} */

/* 按钮 */
.btn-group {
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  display: flex
}

.btn-group .btn {
  flex-grow: 1;
  flex-shrink: 0;
}

.btn {
  padding: 10px 20px;
  border: 3px solid #FF4081;
  display: inline-block;
  position: relative;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  overflow: hidden;
}

/* 按钮focuson */
.btn:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  width: 2px;
  top: 0;
  bottom: 0;
  margin: auto;
  background: #FF4081;
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  z-index: 0;
  opacity: 0;
}

.btn:hover {
  color: #FFF;
}

.btn:hover:before {
  -webkit-transform: scaleX(200);
  -ms-transform: scaleX(200);
  transform: scaleX(200);
  opacity: 1;
}

/* 按钮文字显示 */
.btn span {
  position: relative;
  z-index: 5;
}

/* 头部 */
header {
  overflow: hidden;
  /* background-color: #2d1856;
    opacity: 1; */
}

header .topnav {
  float: right;
  display: flex;
  margin: 30px 0;
}

header .topnav .btn {
  padding: 10px 0px;
  border-radius: 15px;
  margin: 0px 30px;
  width: 150px;
}

